import React, {useState} from "react"

const AboutUs = () => {
    const [readMore, setReadMore] = useState(false)

    return (
        <div className="font-primary grid grid-cols-1 grid-rows-2 md:grid-cols-3 md:grid-rows-1 mt-8 py-8 border-t-2 border-black">
            <div className="hidden h-full md:flex md:justify-center md:items-center md:block">
                <img src={`${process.env.REACT_APP_ASSETS_URL}/aboutUs_img.jpg`} alt="Art by Ravinder Reddy"/>
            </div>
            <div className="row-span-2 px-8 py-4 md:col-span-2">
                <h1 className="text-3xl md:text-4xl">About Tangerine</h1>
                <div className="h-2 w-16 bg-primary md:ml-4"></div>
                <p className="text-secondary md:pl-4 py-4 text-xl md:text-2xl text-left md:text-justify">
                    Tangerine was established with an objective 
                    to promote art and create an aesthetic 
                    awareness. We seek to bring art out of the 
                    confines of an esoteric gallery space and 
                    have been working with an evangelistic zeal to 
                    demystify art and to make it accessible to the 
                    commoner as much as to the connoisseur. We 
                    have been fervent crusaders campaigning for 
                    art as much for investment as for its aesthetic 
                    appeal.
                    <br />
                    We believe that <span onClick={() => setReadMore((readMore) => !readMore)}>
                        {!readMore ? <span className="font-bold cursor-pointer">...read more</span> : "though art has traditionally been the prerogative of the privileged, it is only a myth that it is meant for the super rich. Today, our core groups of investors are the young, adventurous and dynamic high fliers who realize the value, advantage, prestige and privilege of investing in art. Our core groups of artists are the colossal talents from frontline art schools from across the country whose works are affordable and still poised for a quantum leap in value in the foreseeable future. We have a team of advisors with a vast repertoire of experience and Team Tangerine is confident of helping people make both financially and aesthetically prudent art investment decisions"}
                    </span>
                </p>
            </div>
        </div>
    )
}

export default AboutUs