import { IconContext } from "react-icons"
import { FaFacebookF, FaInstagram, FaTwitter } from "react-icons/fa"

const Footer = () => {
    return (
        <div className="font-primary grid grid-cols-1 grid-rows-1 md:grid-cols-2 md:grid-rows-1 py-8 border-t-2 border-black">
            <div className="hidden md:block md:flex md:justify-center md:items-center md:block">
                <img src={`${process.env.REACT_APP_ASSETS_URL}/footer_img.jfif`} alt="Art by Zarina Hashmi" className="h-full w-full object-fill" />
            </div>
            <div className="flex flex-col justify-center items-center">
                <div className="flex flex-col justify-center items-center">
                    <p className="text-2xl md:text-3xl py-2">TANGERINE ART</p>
                    <p className="text-xl md:text-2xl">103, Kumar Paradise</p>
                    <p className="text-xl md:text-2xl">BP Wadia Road, Basavangudi,</p>
                    <p className="text-xl md:text-2xl">Bangalore 560004</p>
                </div>
                <div className="flex justify-center my-8">
                    <div className="h-2 w-16 bg-primary md:ml-4"></div>
                </div>
                <div className="flex flex-col justify-center items-center">
                    <p className="text-xl md:text-2xl">WhatsApp</p>
                    <p className="text-xl md:text-2xl">+91-98862-18518</p>
                    <p className="text-xl md:text-2xl">info@tangerineartspace.com</p>
                </div>
                <div className="block flex justify-center my-8">
                    <div className="h-2 w-16 bg-primary md:ml-4"></div>
                </div>
                <div className="flex justify-center">
                    <a href="" target="_blank" rel="noreferrer" className="mx-6">
                        <IconContext.Provider value={{size: "40px", color: "#59595b"}}>
                            <FaFacebookF />
                        </IconContext.Provider> 
                    </a>
                    <a href="" target="_blank" rel="noreferrer" className="mx-6">
                        <IconContext.Provider value={{size: "40px", color: "#59595b"}}>
                            <FaInstagram />
                        </IconContext.Provider>        
                    </a>
                    <a href="" target="_blank" rel="noreferrer" className="mx-6">
                        <IconContext.Provider value={{size: "40px", color: "#59595b"}}>
                            <FaTwitter />
                        </IconContext.Provider>    
                    </a>
                </div>
            </div>
        </div>
    )
}

export default Footer