import React, { useEffect, useState } from "react"
import gallery from "../../../gallery"

const ImageCarousel = () => {
    //const [selectedImgIndex, setSelectedImgIndex] = useState(0)
    const [newArray, setNewArray] = useState(gallery.slice(0, 5))
    const [selectedImg, setSelectedImg] = useState(newArray[0])

    useEffect(() => {
        setSelectedImg(newArray[0])
    }, [newArray])

    const handleImageSelect = (img) => {
        setNewArray(gallery.slice(img.id-1, (img.id-1) + 5))
    }

    const handlePrev = () => {
        if(selectedImg.id === 1) {
            return
        } else {
            setNewArray(gallery.slice(selectedImg.id-2, (selectedImg.id-2) + 5))
        }
    }

    const handleNext = () => {
        if(selectedImg.id === gallery.length) {
            return
        } else {
            setNewArray(gallery.slice(selectedImg.id, selectedImg.id + 5))
        }
    }

    return (
        <div>
            <div className="h-96">
                <img src={`${process.env.REACT_APP_ASSETS_URL}/${selectedImg.folder}/${selectedImg.fileName}`} alt={`${selectedImg.name}`} className="h-full w-full object-fill"/>
            </div>
            <div className="grid grid-cols-5 overflow-hidden gap-2 mt-3 relative">
                {newArray.map((img) => {
                    return (
                        <div key={img.id} className={`h-full w-full cursor-pointer ${selectedImg.id === img.id ? "border-4 border-primary" : null}`} onClick={() => handleImageSelect(img)}>
                            <img src={`${process.env.REACT_APP_ASSETS_URL}/${img.folder}/${img.fileName}`} alt={`${img.name}`} className="h-full w-full object-fill"/>
                        </div>
                    )
                })}
                <svg xmlns="http://www.w3.org/2000/svg" className="absolute top-1/3 md:top-1/2 bg-white h-5 w-5 md:h-10 md:w-10 mx-4 cursor-pointer" fill="none" viewBox="0 0 24 24"   stroke="currentColor" onClick={handlePrev}>
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 19l-7-7 7-7" />
                </svg>
                <svg xmlns="http://www.w3.org/2000/svg" className="absolute right-0 top-1/3 md:top-1/2 bg-white h-5 w-5 md:h-10 md:w-10 mx-4 cursor-pointer" fill="none" viewBox="0 0 24 24" stroke="currentColor" onClick={handleNext}>
  `                 <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 5l7 7-7 7" />
                </svg>
            </div>
        </div>
    )
}

export default ImageCarousel