const SliderCard = ({ img }) => {
    return (
        <div className="font-primary mt-8 h-96 grid grid-cols-1 grid-rows-3 gap-y-4 md:grid-cols-3 md:grid-rows-1">
            <div className="flex justify-center items-center row-span-2 md:col-span-2">
                <img src={`${process.env.REACT_APP_ASSETS_URL}/${img.folder}/${img.fileName}`} alt={img.name} className="h-full w-full object-fill" />
            </div>
            <div className="flex justify-center items-center flex-col">
                <p className="text-xl">{img.artist}</p>
                <p className="text-xl">{img.name}</p>
                <p className="text-xl">{img.dimension}</p>
                <p className="text-xl">{img.description}</p>
                <p className="text-xl">{img.year}</p>
            </div>
        </div>
    )
}

export default SliderCard